import * as React from "react";
import style from "./Header.module.scss"
import logo from "../../assets/png/A1_Header/logo.png";
import {observer} from "mobx-react-lite";
import {useStore} from "../../store/useStore";
import {svgIcons} from "../../assets/svgIcons";
import {links} from "./links";
import {HashLink} from "react-router-hash-link";
import clsx from "clsx";

export const Header = observer(() => {
    const {
        burgerMenu, setBurgerMenu,
        pageYOffset, scrollDown
    } = useStore();

    return (
        <header className={clsx({
            [style.header]: true,
            [style.header_scroll]: pageYOffset > 110,
            [style.header_hide]: pageYOffset > 110 && scrollDown && !burgerMenu,
        })}>
            <div className={style.logo}>
                <img src={logo} alt=""/>
                <p>DHF</p>
            </div>

            <div className={style.links}>
                {
                    links.map(({to, label}, key) => (
                        <HashLink key={key}
                                  smooth
                                  to={to}
                                  className={style.link}
                        >
                            {label}
                        </HashLink>
                    ))
                }
            </div>

            <div className={style.right}>
                <button className={style.launchBtn}>
                    <p>Launch App</p>
                </button>

                <button className={style.burgerBtn}
                        onClick={() => setBurgerMenu(!burgerMenu)}
                >
                    {
                        burgerMenu ? svgIcons.close : svgIcons.burger
                    }
                </button>
            </div>
        </header>
    )
})
